import { Injectable } from '@angular/core';
import { ModalController } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class ModalHandlerService {

  private modal: HTMLIonModalElement

  constructor(
    private modalController: ModalController
  ) { }

  async createModal(options){this.modal = await this.modalController.create(options);}

  async presentModal(){return await this.modal.present();}

  dismissModal() { this.modal.dismiss() }
  
  onDismissModal() { return this.modal.onDidDismiss(); }


}
