import { TranslateService } from '@ngx-translate/core';
import { OrderState } from './../../../shared/models/profile.model';
import { AlertService } from './../../../shared/services/alert/alert.service';
import { GlobalDataService } from 'src/app/shared/services/globalData/global-data.service';
import { RewardService } from './../services/reward.service';
import { Reward } from './../../../shared/models/reward.model';
import { Component, EventEmitter, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ModalHandlerService } from 'src/app/shared/services/modal-handler/modal-handler.service';
import { IonRouterOutlet } from '@ionic/angular';
import { PrizeTypeModalComponent } from '../prize-type-modal/prize-type-modal.component';
import { RouterConstants } from 'src/app/shared/config/constants/router.constants';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-prize-detail',
  templateUrl: './prize-detail.page.html',
  styleUrls: ['./prize-detail.page.scss'],
})
export class PrizeDetailPage implements OnInit {

  item: any;
  id: number;
  typeDataReward: any;
  officeData: any;

  loadingContent: boolean;
  loading: boolean;
  OrderState = OrderState;

  environment = environment;
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private rewardService: RewardService,
    public globalDataService: GlobalDataService,
    private modalHandler: ModalHandlerService,
    private routerOutlet: IonRouterOutlet,
    private alertService: AlertService,
    private translate: TranslateService,
  ) { }

  ngOnInit() {
    this.route.paramMap.subscribe((params) => {
      this.id = parseInt(params.get('id'));
      this.getReward();
    });
  }

  navigateToOrderList(element) {
    this.router.navigateByUrl(RouterConstants.PROFILE_ORDERS_DETAIL + element.id);
  }

  async getReward() {
    try {
      this.loadingContent = true;
      this.item = await this.rewardService.getUserReward(this.id).toPromise();
      console.log('Reward ', this.item);
      this.loadingContent = false;
    } catch (error) {
      console.log(error);
      this.loadingContent = false;
    }
  }

  async checkoutPrize() {
    try {
      this.loading = true;

      const buttons = [
        { text: this.translate.instant('ALERTS.CANCEL'), role: 'cancel', },
        { text: this.translate.instant('ALERTS.ACCEPT'), role: 'accept' }
      ];
      let result:any = await this.alertService.showGameAlert(this.translate.instant('PAGES.PRIZES.DETAIL.RECEIVE'), this.translate.instant('PAGES.PRIZES.DETAIL.RECEIVE_ASK')+this.item?.points+" m "+ this.translate.instant('PAGES.PRIZES.DETAIL.BALANCE') +(this.globalDataService.player.magentoPoints-this.item?.points)+" m.", buttons);
      if (result && result.data && result.role === 'accept') {
        let data = await this.rewardService.redeemReward(this.id).toPromise();
        this.officeData = undefined;
        this.globalDataService.player.magentoPoints = data.magentoPoints;
        this.globalDataService.player.pointsFreeze = data.magentoPoints;
        this.item = data.reward;
        console.log('Redeem ', data);
      }
      this.loading = false;
    } catch (error) {
      console.log(error);
      this.loading = false;
    }
  }

  async openRewardType() {
    if (this.item?.points < this.globalDataService.player.magentoPoints) {
      
      const eventFunction = new EventEmitter();

      await this.modalHandler.createModal({
        component: PrizeTypeModalComponent,
        swipeToClose: true,
        mode: 'ios',
        cssClass: 'swipeableModal',
        // breakpoints: [0, 0.5, 0.75],
        // initialBreakpoint: 0.5,
        presentingElement: this.routerOutlet.nativeEl,
        componentProps: {
          type: this.typeDataReward,
          product: this.item,
          eventFunction: eventFunction,
        }
      });

      eventFunction.subscribe(data => {
        this.officeData = data;
        console.log('RewardType', this.officeData);
        this.modalHandler.dismissModal();
      });
      return this.modalHandler.presentModal();
    }
  }

  dateToStringShort(date: any) {
    date = new Date(date);
    return (("0" + (date.getDate())).slice(-2)) + "/" + (("0" + (date.getMonth() + 1)).slice(-2)) + "/" + date.getFullYear().toString().substr(-2,4);
  }
  
}
